.nav {
    grid-area: nav;
    background-color: rgb(157, 171, 248);
    padding: 10px 10px 10px 15px;
}

.nav .item a {
    color: white;
    text-decoration: none;
    font-family: "Freestyle Script";
    font-size: 40px;
    font-weight: bold;
    transition: 0.25s;
}

.item a.active {
    color: black;
}
.nav .item a:hover{
    padding: 0px 0px 0px 10px;
}

.setting {
    padding: 30px 0px 100px 0px;
}

.friends a {
    font-size: 50px !important;
}

.bestFriends {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 0px 5px 0px 0px;

}