*{
    box-sizing: border-box;
}
.header {
    grid-area: header;
    background-color: chartreuse;
}

.header img {
    width: 45px;
    padding: 3px;
    margin: 0px 0px 0px 10px;
}
.loginBlock{
    margin: 10px 10px 0px 0px;
    padding: 10px;
    float: right;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.loginBlock a{
    color: black;
    font-size: larger;
}
.logoBrand{
    border-radius: 5% 30% 5% 30%;
}