.content {
    grid-area: content;
    display: grid;
    background-color: darkorange;
    grid-template-areas: "content_img"
                        "content_logo"
    "content_posts";
    grid-template-rows: 2fr 2fr 2fr;
    grid-template-columns: 12fr;
}
/*@media (max-width: 1050px){*/
/*.content__img{*/
/*    max-width: 90%;*/
/*}*/


.content__img {
    grid-area: content_img;
    display: flex;
    justify-content: center;
    height: 250px;
    width: 100%;
    overflow: hidden;
}

.content__img video {
    width: 1000px;
    height: 225%;
}

.description {
    padding: 10px;
    grid-area: content_logo;
    min-width: 100px;
    display: grid;
    grid-template-rows: 180px;
    grid-template-columns: 2fr 10fr;
    grid-template-areas: "content_logo content_description";
}

.logo {
    grid-area: content_logo;
    width: 100%;
    height: 100%;
}

.logo img {
    width: 100%;
    height: 100%;
    border-radius: 25%;
}

.descriptionText {
    grid-area: content_description;
    width: 80%;
    /*max-width: 600px;*/
    padding: 10px 0px 0px 15px;
}
.status{
    padding: 10px 0px 10px 15px;
    font-size: 20px;
    color: white;
}
